.kehoach-content ul {
    counter-reset: li; 
    list-style: none; 
    padding: 0;
    text-shadow: 0 1px 0 rgba(255,255,255,.5);
}

.kehoach-content ul .semester_header {
position: relative;
display: block;
padding: .4em .4em .4em .8em;
margin: 0.5em 0 .6em 0;
background: #c2daf8;
color: rgb(51, 51, 51);
font-size: 20px;
font-weight: 500;
text-decoration: none;
transition: all .3s ease-out;
}

.kehoach-content ul .semester_header:hover {background: #f9dd94;}   

.kehoach-content ul .semester_header p{
    margin: auto 0;
}

.kehoach-content ul .semester_header .btn_container{
   margin-left: 10px;
}
